import React, { Component } from "react";
import Header from "../header/header";
import Footer from "../footer/footer";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import cssClass from "./payment.module.css";
import axios from "../../axiosConfig";
import { Tabs } from "antd";
import { Input } from "antd";
import { css } from "styled-components";
import { Collapse } from 'antd';

const { Panel } = Collapse;
const PriceCalculate = React.lazy(() =>
  import("../price-calculate/price-calculate")
);
const { TabPane } = Tabs;
class Payment extends Component {
  constructor() {
    super();
    this.state = {
      years: [],
      amount: 0,
      activeKey: "1",
      data: {},
      paymentMode: "card",
      cardOptions: [],
      netbankingOptions: [],
      walletOpions: [],
      upi: false,
      emiOpions: [],
      cardless_emiOpions: [],
      paylaterOptions: [],
    };
    var currentYear = new Date().getFullYear();
    for (let i = currentYear; i < currentYear + 35; i++) {
      this.state.years.push(i);
    }
  }
  componentDidMount() {
    this.props.cartUpdate(this.props.location.pathname);
    this.initPaymentMethod();
  }
  initPaymentMethod() {
    var self = this;
    var razorpay = new window.Razorpay({
      key: "rzp_live_ILgsfZCZoFIKMb", // LIVE: rzp_live_ILgsfZCZoFIKMb TEST:rzp_test_38RonBOwwEa2xh
      // logo, displayed in the payment processing popup
      image: "https://i.imgur.com/n5tjHFD.png",
    });
    razorpay.once("ready", function (response) {
      //  console.log(response.methods['card_networks'])
      self.setState(
        {
          cardOptions: response.methods["card_networks"],
          netbankingOptions: response.methods["netbanking"],
          walletOpions: response.methods["wallet"],
          upi: response.methods.upi,
          upi_intent: response.methods.upi_intent,
          emi: response.methods.emi,
          cardless_emiOpions: response.methods["cardless_emi"],
          paylaterOptions: response.methods["paylater"],
        },
        () => {
          console.log(response.methods);
        }
      );
    });
  }
  onChange = (activeKey) => {
    this.setState(
      {
        activeKey: activeKey,
      },
      () => {}
    );
  };
  formOnChange = (event) => {
    console.log(event.target.name);
  };
  initPayment = (event) => {
    const params = {
      amount: (this.props.orderTotal + this.props.deliveryCharge) * 100,
      curreny: "INR",
    };
    axios.post("razorpay", params).then((res) => {
      var razorpay = new window.Razorpay({
        key: "rzp_test_38RonBOwwEa2xh", // LIVE: rzp_live_ILgsfZCZoFIKMb TEST:rzp_test_38RonBOwwEa2xh
        // logo, displayed in the payment processing popup
        image: "https://i.imgur.com/n5tjHFD.png",
      });
      var data = {};
      if (this.state.activeKey === "1") {
        // for card
        data = {
          amount: res.data.amount, // in currency subunits. Here 1000 = 1000 paise, which equals to 10
          order_id: res.data.order_id, // Replace with Order ID generated in Step 4
          email: "mdirfan844@gmail.com",
          contact: "9836372183",
          notes: {
            address: "Shaheen Bagh Okhla New Delhi",
          },
          method: "card",
          "card[name]": "Gaurav Kumar",
          "card[number]": "4111111111111111",
          "card[expiry_month]": "12",
          "card[expiry_year]": "20",
          "card[cvv]": "100",
        };
      } else if (this.state.activeKey === "2") {
        // for UPI
        data = {
          // useing Collect Flow
          amount: res.data.amount,
          order_id: res.data.order_id, //sample order_id. Generate orders using Orders API
          email: "gaurav.kumar@example.com",
          contact: "9123456789",
          method: "upi",
          upi: {
            flow: "intent",
          },
        };
        // using Intent Flow implemented later
      } else if (this.state.activeKey === "3") {
        // for wallet
        data = {
          amount: res.data.amount,
          order_id: res.data.order_id, //sample order_id. Generate orders using Orders API
          email: "gaurav.kumar@example.com",
          contact: "9123456789",
          method: "wallet",
          wallet: "mobikwik",
        };
      } else if (this.state.activeKey === "4") {
        data = {
          amount: res.data.amount, // in currency subunits. Here 1000 = 1000 paise, which equals to 10
          order_id: res.data.order_id, // Replace with Order ID generated in Step 4
          email: "mdirfan844@gmail.com",
          contact: "9836372183",
          notes: {
            address: "Shaheen Bagh Okhla New Delhi",
          },
          method: "netbanking",
          // method specific fields
          bank: "SBIN", // bank code
        };
      } else if (this.state.activeKey === "5") {
        // fro EMI on credit card
        data = {
          amount: res.data.amount, // in currency subunits. Here 1000 = 1000 paise, which equals to 10
          order_id: res.data.order_id, // Replace with Order ID generated in Step 4
          email: "gaurav.kumar@example.com",
          contact: "9123456789",
          method: "emi",
          emi_duration: 6, //defines the number of months for the EMI.
          "card[name]": "Gaurav Kumar",
          "card[number]": "5241810000000000",
          "card[expiry_month]": "12",
          "card[expiry_year]": "20",
          "card[cvv]": "100",
        };
      } else if (this.state.activeKey === "6") {
        // alert("cod");
      }
      razorpay.createPayment(data);
      razorpay.on("payment.success", function (resp) {
        // alert(resp.razorpay_payment_id);
        // alert(resp.razorpay_order_id);
        // alert(resp.razorpay_signature);
      }); // will pass payment ID, order ID, and Razorpay signature to success handler.

      razorpay.on("payment.error", function (resp) {
        // alert(resp.error.description);
      }); // will pass error object to error handler
    });
  };

  render() {
    localStorage.setItem("curUrl", this.props.match.url);

    let tabPosition;
    if (window.innerWidth == "32em") {
      tabPosition = "top";
    } else {
      tabPosition = "left";
    }
    return (
      <div className="App">
        <Header />
        <div className="body">
          <hr />
          <div className="container-fluid">
            <div className={`row ${cssClass.paymentBody}`}>
              <div className="col-md-8 col-sm-8 col-12 ">
                <div className="pt-4">
                  <strong>Choose Payment Mode</strong>
                </div>
                {/* <br /> */}
                <div
                  className="card-container"
                  style={{ borderTop: "1px solid whitesmoke" }}
                >
                  <Tabs
                    onChange={this.onChange}
                    type="card"
                    tabPosition='left'
                    tabBarGutter="0"
                    size="large"
                    
                  >
                    <TabPane
                      tab={
                        <div className={` ${cssClass.tabPane}`}>
                          CREDIT/DEBIT CARD
                        </div>
                      }
                      key="1"
                    >
                      <div className = {`${cssClass.PaymentForm} pt-5`}>
                        <div>
                          <strong>CREDIT/DEBIT CARD </strong>
                        </div>
                        <hr />
                        <form onChange={this.formOnChange} className="">
                          <span>Card Number*</span>
                          <Input name="cardNumber" size="large" />
                          <br />
                          <br />
                          <span>Name on Card*</span>
                          <Input name="nameOnCard" size="large" />
                          <br />
                          <br />
                          <div className="row">
                            <div className="col-sm-6 col-6">
                              <span>Expiry Month*</span>
                              <br />
                              <select className={cssClass.selectDropdown}>
                                <option value="0">Expiry month</option>
                                <option name="JAN" value="1">
                                  01 (JAN)
                                </option>
                                <option name="FEB" value="2">
                                  02 (FEB)
                                </option>
                                <option name="MAR" value="3">
                                  03 (MAR)
                                </option>
                                <option name="APR" value="4">
                                  04 (APR)
                                </option>
                                <option name="MAY" value="5">
                                  05 (MAY)
                                </option>
                                <option name="JUN" value="6">
                                  06 (JUN)
                                </option>
                                <option name="JUL" value="7">
                                  07 (JUL)
                                </option>
                                <option name="AUG" value="8">
                                  08 (AUG)
                                </option>
                                <option name="SEP" value="9">
                                  09 (SEP)
                                </option>
                                <option name="OCT" value="10">
                                  10 (OCT)
                                </option>
                                <option name="NOV" value="11">
                                  11 (NOV)
                                </option>
                                <option name="DEC" value="11">
                                  12 (DEC)
                                </option>
                              </select>
                            </div>
                            <div className="col-sm-6 col-6">
                              <span>Expiry Year*</span>
                              <br />
                              <select className={cssClass.selectDropdown}>
                                <option value="0">Expiry year</option>
                                {this.state.years.map((year) => (
                                  <option>{year}</option>
                                ))}
                              </select>
                            </div>
                          </div>
                          <div className="row">
                            <div className="col-sm-4 col-6">
                              CVV*
                              <Input name="cvv" size="large" />
                              <br />
                              <br />
                            </div>
                            <div className="col-sm-8 col-6">
                              <br />
                              Last 3 digits printed on the back of the card
                            </div>
                          </div>
                        </form>
                        <button
                          onClick={this.initPayment}
                          className="w-100 p-2">
                          Pay Now
                        </button>
                      </div>
                    </TabPane>

                    <TabPane
                      tab={<div className={cssClass.tabPane}>UPI</div>}
                      key="2"
                    >
                      <div className="pt-3">
                        <button
                          onClick={this.initPayment}
                          className="w-100 p-2"
                        >
                          Pay Now
                        </button>
                      </div>
                    </TabPane>
                    <TabPane
                      tab={<div className={cssClass.tabPane}>WALLETS</div>}
                      key="3"
                    >
                      <div className="pt-3">
                        <button
                          onClick={this.initPayment}
                          className="w-100 p-2"
                        >
                          Pay Now
                        </button>
                      </div>
                    </TabPane>
                    <TabPane
                      tab={<div className={cssClass.tabPane}>NET BANKING</div>}
                      key="4"
                    >
                      <div className="pt-3">
                        <button
                          onClick={this.initPayment}
                          className="w-100 p-2"
                        >
                          Pay Now
                        </button>
                      </div>
                    </TabPane>
                    <TabPane
                      tab={<div className={cssClass.tabPane}>EMI</div>}
                      key="5"
                    >
                      <div className="pt-3">
                        <button
                          onClick={this.initPayment}
                          className="w-100 p-2"
                        >
                          Pay Now
                        </button>
                      </div>
                    </TabPane>
                    <TabPane
                      tab={
                        <div className={cssClass.tabPane}>CASH ON DELIVERY</div>
                      }
                      key="6"
                      style={{ padding: "20px" }}
                    >
                      <div className="pt-3">
                        <button
                          onClick={this.initPayment}
                          className="w-100 p-2"
                        >
                          Pay Now
                        </button>
                      </div>
                    </TabPane>
                   
                  </Tabs>
                </div>
              </div>
              <div className="col-md-4 col-sm-4 col-12">
                <PriceCalculate />
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    BagTotal: state.BagTotal,
    BagDiscount: state.BagDiscount,
    couponDiscount: state.couponDiscount,
    orderTotal: state.orderTotal,
    deliveryCharge: state.deliveryCharge,
    currentComponent: state.currentComponent,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    cartUpdate: (currentComponent) => {
      dispatch({
        type: "UPDATE_COMPONENT",
        payload: {
          currentComponent,
        },
      });
    },
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(Payment);
